@use '../../scss/' as *;

.movie-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-bottom: 3rem;

    @include tablet{
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }

    &__viewmore{
        text-align: center;
    }
}

.movie-search{
    position: relative;
    width: 100%;
    max-width: 500px;

    input{
        width: 100%;
        padding-right: 8rem;
    }

    button{
        position: absolute;
        right: 0;
        top: 2px;

        @include mobile{
            right: 1px;
            top: 1px;
        }
    }
}